/* dropdownMenu */
.NoteDropdownMenuOverlay {
  background-color: transparent !important;
}
.dropdownMenu > .circle {
  width: 3px;
  height: 3px;
  margin: 3px;
  background: var(--main-black);
  border-radius: 50%;
  display: block;
}
.dropdownMenuWrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.dropdownMenuWrapper > div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.dropdownMenuWrapper > div:hover,
.dropdownMenuWrapper > div:focus {
  outline: none;
}
.dropdownMenu {
  border-radius: 0.25rem;
  display: flex;
  flex-wrap: wrap;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  flex: 1;
  flex-direction: column;
  position: relative;
  transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.dropdownMenu > .circle:nth-child(4) {
  position: absolute;
  opacity: 0;
  margin-left: -15px;
}
.dropdownMenu > .circle:nth-child(5) {
  position: absolute;
  opacity: 0;
  margin-right: -15px;
}
.dropdownMenuClicked {
  transform: rotate(45deg);
  opacity: 1;
}
.dropdownMenuClicked > .circle {
  opacity: 1 !important;
}

/* END OF dropdownMenu SECTION */

